body,
html {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: #00b4ff;
  max-width: 100%;
  overflow-x: hidden;
  height: 100%;
  min-height: 100%; }

h1 {
  position: relative;
  z-index: 2;
  font-size: 30px;
  color: #ac5975;
  text-decoration: underline; }

.wrapper {
  position: relative;
  height: 22vh; }

.windmill {
  margin-top: 100px;
  width: 300px;
  height: 350px;
  margin: 0 20vw;
  position: absolute;
  bottom: 0; }

/* KEYFRAMES */
@keyframes rotateWindmill {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(-360deg); } }

/* OBJECTS */
.post {
  position: absolute;
  width: 10px;
  height: 220px;
  background: linear-gradient(to top, #ffffff 0%, #cccccc 100%);
  top: 145px;
  left: 145px;
  border-radius: 3px; }

.engine {
  position: absolute;
  width: 20px;
  height: 20px;
  background: linear-gradient(to bottom, #ffffff 0%, #cccccc 100%);
  top: 130px;
  left: 140px; }

.head {
  width: 10px;
  height: 10px;
  position: absolute;
  top: 135px;
  left: 145px;
  z-index: 1000;
  background: radial-gradient(center, #ffffff 0%, #999999 100%);
  border-radius: 50%; }

.flap {
  width: 5px;
  height: 120px;
  position: absolute;
  background: linear-gradient(to bottom, #ffffff 0%, #cccccc 100%);
  border-top-left-radius: 5px 20px; }
