body,
html {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: #00b4ff;
  max-width: 100%;
  overflow-x: hidden;
  height: 100%;
  min-height: 100%; }

h1 {
  position: relative;
  z-index: 2;
  font-size: 30px;
  color: #ac5975;
  text-decoration: underline; }

.wrapper {
  position: relative;
  height: 22vh; }

.current-weather {
  font-size: 17px;
  text-align: center;
  width: 40vw;
  margin: 0 auto;
  border-radius: 10px 10px 0 0;
  position: relative;
  padding: 0.5rem;
  background-color: rgba(255, 243, 117, 0.5);
  z-index: 2; }
  .current-weather h2,
  .current-weather p {
    color: #ac5975; }
  .current-weather span {
    font-weight: bold;
    font-size: 19px; }
