.clouds {
  bottom: 0;
  left: 0;
  padding-top: 50px;
  position: absolute;
  right: 0;
  z-index: 1; }

/* KEYFRAMES */
@-webkit-keyframes animateCloud {
  0% {
    margin-left: -1000px; }
  100% {
    margin-left: 100%; } }
@keyframes animateCloud {
  0% {
    margin-left: -1000px; }
  100% {
    margin-left: 100%; } }

/* ANIMATIONS */
/* OBJECTS */
.cloud {
  background: #ffffff;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #ffffff), to(#f1f1f1));
  background: -webkit-linear-gradient(top, #ffffff 5%, #f1f1f1 100%);
  background: linear-gradient(to bottom, #ffffff 5%, #f1f1f1 100%);
  border-radius: 100px;
  box-shadow: 0 8px 5px rgba(0, 0, 0, 0.1);
  height: 120px;
  position: relative;
  width: 350px; }

.cloud:after,
.cloud:before {
  background: #ffffff;
  content: "";
  position: absolute;
  z-index: -1; }

.cloud:after {
  border-radius: 100px;
  height: 100px;
  left: 50px;
  top: -50px;
  width: 100px; }

.cloud:before {
  border-radius: 200px;
  width: 180px;
  height: 180px;
  right: 50px;
  top: -90px; }

