body,
html {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: #00b4ff;
  max-width: 100%;
  overflow-x: hidden;
  height: 100%;
  min-height: 100%; }

h1 {
  position: relative;
  z-index: 2;
  font-size: 30px;
  color: #ac5975;
  text-decoration: underline; }

.wrapper {
  position: relative;
  height: 22vh; }

.flex-wrapper {
  display: flex;
  justify-content: center;
  padding: 30px;
  font-size: 17px;
  text-align: center;
  width: 40vw;
  margin: 0 auto;
  border-radius: 0 0 10px 10px;
  position: relative;
  padding: 0.5rem;
  background-color: rgba(216, 112, 147, 0.6);
  z-index: 2; }

.grid-wrapper {
  display: grid;
  max-width: 800px;
  grid-gap: 20px;
  grid-template-columns: auto auto auto auto auto auto;
  padding: 10px;
  color: #ffffff;
  font-weight: bold; }
  .grid-wrapper span {
    font-weight: normal; }
