@import "src/index.scss";

.windmill {
  margin-top: 100px;
  width: 300px;
  height: 350px;
  margin: 0 20vw;
  position: absolute;
  bottom: 0;
}

/* KEYFRAMES */

@keyframes rotateWindmill {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

/* OBJECTS */

.post {
  position: absolute;
  width: 10px;
  height: 220px;
  background: linear-gradient(
    to top,
    $color-white 0%,
    darken($color-white, 20%) 100%
  );
  top: 145px;
  left: 145px;
  border-radius: 3px;
}

.engine {
  position: absolute;
  width: 20px;
  height: 20px;
  background: linear-gradient(
    to bottom,
    $color-white 0%,
    darken($color-white, 20%) 100%
  );
  top: 130px;
  left: 140px;
}

.head {
  width: 10px;
  height: 10px;
  position: absolute;
  top: 135px;
  left: 145px;
  z-index: 1000;
  background: radial-gradient(
    center,
    $color-white 0%,
    darken($color-white, 40%) 100%
  );
  border-radius: 50%;
}

.flap {
  width: 5px;
  height: 120px;
  position: absolute;
  background: linear-gradient(
    to bottom,
    $color-white 0%,
    darken($color-white, 20%) 100%
  );
  border-top-left-radius: 5px 20px;
}
