body,
html {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: #00b4ff;
  max-width: 100%;
  overflow-x: hidden;
  height: 100%;
  min-height: 100%; }

h1 {
  position: relative;
  z-index: 2;
  font-size: 30px;
  color: #ac5975;
  text-decoration: underline; }

.wrapper {
  position: relative;
  height: 22vh; }

.clouds {
  bottom: 0;
  left: 0;
  padding-top: 50px;
  position: absolute;
  right: 0;
  z-index: 1; }

/* KEYFRAMES */
@keyframes animateCloud {
  0% {
    margin-left: -1000px; }
  100% {
    margin-left: 100%; } }

/* ANIMATIONS */
/* OBJECTS */
.cloud {
  background: #ffffff;
  background: linear-gradient(to bottom, #ffffff 5%, #f1f1f1 100%);
  border-radius: 100px;
  box-shadow: 0 8px 5px rgba(0, 0, 0, 0.1);
  height: 120px;
  position: relative;
  width: 350px; }

.cloud:after,
.cloud:before {
  background: #ffffff;
  content: "";
  position: absolute;
  z-index: -1; }

.cloud:after {
  border-radius: 100px;
  height: 100px;
  left: 50px;
  top: -50px;
  width: 100px; }

.cloud:before {
  border-radius: 200px;
  width: 180px;
  height: 180px;
  right: 50px;
  top: -90px; }
